.container {
  width: 80%;
  max-width: 1200px;
  margin: 20px auto;
}

h1 {
  text-align: center;
  font-size: 24px;
}

.test-list {
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 20px;
  padding: 0;
  list-style: none;
  display: grid;
}

.test-card {
  background: #fff;
  border-radius: 10px;
  flex-direction: column;
  justify-content: space-between;
  padding: 15px;
  transition: transform .2s ease-in-out;
  display: flex;
  position: relative;
  box-shadow: 0 2px 10px #0000001a;
}

.test-card:hover {
  transform: scale(1.02);
}

.test-card p {
  line-clamp: 1;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  display: -webkit-box;
  overflow: hidden;
}

.test-header {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.test-header h2 {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

.menu-button {
  cursor: pointer;
  background: none;
  border: none;
  padding: 5px;
  font-size: 20px;
}

.menu-container {
  position: relative;
}

.dropdown-menu {
  background: #fff;
  border-radius: 5px;
  flex-direction: column;
  display: none;
  position: absolute;
  top: 30px;
  right: 0;
  overflow: hidden;
  box-shadow: 0 2px 10px #0000001a;
}

.dropdown-menu button {
  text-align: left;
  cursor: pointer;
  background: none;
  border: none;
  width: 120px;
  padding: 10px;
  font-size: 14px;
  transition: background .2s ease-in-out;
}

.dropdown-menu button:hover {
  background: #f0f0f0;
}

.menu-container.active .dropdown-menu {
  display: flex;
}

.test-actions {
  gap: 10px;
  display: flex;
}

.test-actions button {
  cursor: pointer;
  border: none;
  border-radius: 5px;
  flex: 1;
  padding: 8px;
  font-size: 14px;
  transition: background .2s ease-in-out;
}

.start {
  text-align: center;
  color: #fff;
  background: #28a745;
  border-radius: .5rem;
  width: 100%;
  padding: .5rem 0;
  text-decoration: none;
  transition: all .2s ease-in;
}

.start-link {
  color: #000;
  font-family: Montserrat, sans-serif;
  text-decoration: none;
}

.start:hover {
  background: #218838;
}

form {
  flex-direction: column;
  gap: 15px;
  display: flex;
}

input, textarea {
  border: 1px solid #ccc;
  border-radius: 5px;
  width: 100%;
  padding: 10px;
  font-size: 16px;
}

textarea {
  resize: vertical;
  height: 100px;
}

#questions-container {
  margin-top: 20px;
}

#add-question {
  color: #fff;
  cursor: pointer;
  background: #007bff;
  border: none;
  border-radius: 5px;
  padding: 10px;
}

#add-question:hover {
  background: #0056b3;
}

.save-test {
  color: #fff;
  cursor: pointer;
  text-align: center;
  background: #28a745;
  border: none;
  border-radius: 5px;
  padding: 12px;
  font-size: 16px;
}

.save-test:hover {
  background: #218838;
}

.sorting-wrapper {
  display: flex;
}

.sorting-text {
  margin-right: 10px;
}

#sort-type {
  background: none;
  border: none;
  border-bottom: 3px solid #28a745;
  outline: none;
}

.rollback {
  color: #000;
  border-left: 1px solid #28a745;
  padding: .5rem 1rem;
  font-weight: bold;
  text-decoration: none;
}

.test-title {
  white-space: nowrap;
  text-overflow: ellipsis;
  max-width: 100%;
  display: block;
  overflow: hidden;
}

.pieChart {
  max-width: 500px;
  max-height: 500px;
  padding: 1rem;
}
